<template>
	<view class="content" :style="{'padding-top':phone.safeArea.top+'px'}">
		<view class="mask" v-if="loading" style="background-color:#F5F5F5;margin-top:0!important;" :style="{'height':loadHeight+'px'}">
			<view class="loading13" style="position:absolute;margin-top:-50upx;">
				<view></view>
				<view></view>
				<view></view>
				<view></view>
				<view></view>
			</view>
		</view>
		
		<view class="head-content" v-if="!isLogin">
			<view class="head-content-top nologin">
				<image src="@/static/imgs/tx0.png" mode="aspectFill" style="width:130upx;height:130upx;border-radius:50%;float:left;margin-top:10upx;"></image>
				<view style="height:130upx;">
					<text class="head-content-login" @click="jumpUrl('login/login', 'new')">登录 / 注册</text>
					<view style="font-size:26upx;color:#666666;height:50upx;line-height:50upx;margin:-10upx 0 0 160upx;">登录后可获得更多精彩内容哦~</view>
				</view>
			</view>
		</view>
		
		<view class="head-content" v-else>
			<view class="head-content-top" @click="jumpUrl('member/setting', 'new')">
				<image class="tx left" :src="userImg" mode="aspectFill"></image>
				<text>{{userInfo.username}}</text>
				<image class="right-img right" src="@/static/imgs/r.png"></image>
			</view>
			<view class="head-content-center">
				<view class="head-content-center-left">
					<image class="left" src="@/static/member/my_gold.png"></image>
					<view style="margin-left:10upx;">
						<text style="line-height:30upx;margin-top:15upx;">{{userInfo.corn}}</text>
						<text style="font-size:26upx;color:rgba(255,255,255,0.7);">我的金币</text>
					</view>
				</view>
				<view class="head-content-center-right">
					<image class="left" src="@/static/member/balance.png"></image>
					<view style="margin-left:10upx;">
						<text style="line-height:30upx;margin-top:15upx;">{{userInfo.money}}</text>
						<text style="font-size:26upx;color:rgba(255,255,255,0.7);">账户余额</text>
					</view>
				</view>
			</view>
			<view class="head-content-bottom">
				<view style="font-size:26upx;color:#666;">
					<text class="user-vip" v-if="userInfo.isVip">VIP</text>
					<text class="user-leve" v-else>普通用户</text>
					<text class="vip-date" v-if="userInfo.isVip">{{exp_time}}</text>
				</view>
				<view style="margin-top:10upx;">
					<text>免费观看次数：{{userInfo.watch}} / {{userInfo.watch_count}}</text>
					<view class="right sign">
						<text class="y-sign" v-if="userInfo.sign">已签到</text>
						<text class="n-sign" v-else @click="userSign">签到</text>
						<text>{{userInfo.signTip}}</text>
					</view>
				</view>
			</view>
		</view>

		<view class="center-content">
			<view class="center-content-item" @click="jumpUrl('member/mymoney', 'new')">
				<image src="@/static/member/account.png"></image>
				<view>账户钱包</view>
			</view>
			<view class="center-content-item" @click="jumpUrl('member/collect', 'new')">
				<image src="@/static/member/collection.png"></image>
				<view>视频收藏</view>
			</view>
			<view class="center-content-item" @click="jumpUrl('member/watch_log', 'new')">
				<image src="@/static/member/watch_log.png"></image>
				<view>我的足迹</view>
			</view>
			<view class="center-content-item" @click="jumpUrl('recharge/recharge?type=1', 'new')">
				<image src="@/static/member/rec.png"></image>
				<view>充值中心</view>
			</view>
			<view class="center-content-item" @click="jumpUrl('game/game_list', 'new')">
				<image src="@/static/member/game.png"></image>
				<view>活动专区</view>
			</view>
			<view class="center-content-item" @click="goHome(userId)">
				<image src="@/static/member/home.png"></image>
				<view>我的主页</view>
			</view>
			<view class="center-content-item" @click="jumpUrl('member/poster', 'tab')">
				<image src="@/static/member/share.png"></image>
				<view>宣传分享</view>
			</view>
			<view class="center-content-item" @click="jumpUrl('upload/index', 'new')">
				<image src="@/static/member/video.png"></image>
				<view>上传视频</view>
			</view>
		</view>
		
		<view class="line"></view>
		
		<view class="bottom-content">
			<view class="bottom-content-line" @click="jumpUrl('setting/setting', 'new')">
				<image class="left-img left" src="@/static/member/seting.png"></image>
				<text>系统设置</text>
				<view style="width:10px;height:10px;border-radius:50%;background:#F9221D;display:inline-block;margin-left:5upx;" v-if="isNewApp"></view>
				<image class="right-img right" src="@/static/imgs/r.png"></image>
			</view>
			<view class="bottom-content-line" @click="jumpUrl('member/setting', 'new')">
				<image class="left-img left" src="@/static/member/user.png"></image>
				<text>账号管理</text>
				<image class="right-img right" src="@/static/imgs/r.png"></image>
			</view>
			<view class="bottom-content-line" @click="jumpUrl('setting/about', 'new')">
				<image class="left-img left" src="@/static/member/about.png"></image>
				<text>关于我们</text>
				<image class="right-img right" src="@/static/imgs/r.png"></image>
			</view>
		</view>
		
		<view class="line"></view>
		
		<view class="content-out" v-if="isLogin" @click="logout">注销登录</view>
		
		<view class="line"></view>
	</view>
</template>

<script>
	import api from "@/common/api.js";
	var _self;
	export default {
		data() {
			return {
				phone: {},
				one: false,
				isLogin: false,
				userId: 0,
				userInfo: {
					nickname: '无名',
					corn: 0,
					money: 0,
					watch: 0,
					watch_count: 0,
				},
				userImg: '/static/imgs/head.png',
				exp_time: '...',
				
				isNewApp: false,
				
				loading: true,
				loadHeight: 600,
			}
		},
		onLoad() {
			_self = this;
			//uni.showLoading({ title:'载入中...',mask:true });
			_self.phone = uni.getSystemInfoSync();
			_self.loadHeight = _self.phone.windowHeight;
			_self.isLogin = api.getLogins();
			if(_self.isLogin){
				_self.userId = _self.isLogin.userId;
				_self.getUserInfo();
			}else{
				_self.loading = false;
			}
			uni.getStorage({
			    key: 'isNewVersion',
			    success: (res)=> {
					_self.isNewApp = res.data;
			    }
			});
		},
		onHide() {
			//console.log('onHide');
			_self.one = true;
		},
		onShow() {
			_self.isLogin = api.getLogins();
			if(_self.isLogin && _self.one){
				_self.userId = _self.isLogin.userId;
				_self.getUserInfo();
			}
			uni.setStorage({
			    key: 'upload_'+api.appkey,
			    data: false,
			    success: ()=> {
			        //console.log('success');
			    }
			});
		},
		methods: {
			goHome(uid) {
				if(uid == 0){
					api.showToast('请先登录账号');
					setTimeout(() => {
						_self.jumpUrl('login/login', 'new');
					}, 500);
					return
				}
				var param = {
					url: '/pages/member/member',
					t: 'tab'
				}
				api.jumpUrl('/pages/member/home_page?type='+JSON.stringify(param)+'&uid='+uid, 'new');
			},
			getUserInfo() {
				uni.request({
					url: api.apiData.getInfo,
					method: 'POST',
					data: {
						userId: _self.userId
					},
					header: {
						'Content-type': 'application/x-www-form-urlencoded'
					},
					success: (e)=> {
						//console.log(e)
						if(e.data.Code == 200) {
							_self.userInfo = e.data.Data;
							_self.userImg = (_self.userInfo.avatar == '') ? '/static/imgs/head.png' : _self.userInfo.avatar;
							_self.userInfo.nickname = (_self.userInfo.nickname == '') ? _self.userInfo.username : _self.userInfo.nickname;
							if(_self.userInfo.is_permanent == 1){
								_self.exp_time = "尊贵的永久VIP会员";
							}else if(_self.userInfo.isVip){
								_self.exp_time = '到期时间：'+_self.userInfo.vipEndTime;
							}else{
								_self.exp_time = '升级VIP，全站视频任意看';
							} 
						}else{
							api.delLogins();
							_self.isLogin = false;
						}
						setTimeout(()=>{
							_self.loading = false;
						}, 300)
						//uni.hideLoading();
					},
					fail: ()=> {
						// 加载失败提示
						api.showToast('服务器连接失败，请检查网络是否正常', 3000);
					}
				});
			},
			userSign() {
				uni.showLoading({ title:'签到中...',mask:true });
				uni.request({
					url: api.apiData.userSign+'/userId/'+_self.userId,
					method: 'GET',
					header: {
						'Content-type': 'application/x-www-form-urlencoded'
					},
					success: (e)=> {
						var res = e.data;
						api.showToast(res.Msg, 3000);
						if(res.Code == 200) _self.userInfo.sign = true;
					},
					fail: () => {
						// 加载失败提示
						api.showToast('服务器连接失败，请检查网络是否正常', 3000);
					}
				});
			},
			logout() {
				//uni.showLoading({ title:'注销中...',mask:true });
				uni.showModal({
				    title: '提示',
					confirmText: '退出登录',
					cancelText: '点错了',
				    content: '确定退出登录吗？',
				    success: (res)=> {
				        if(res.confirm) {
				            if(api.delLogins()) {
								api.showToast('退出成功');
								setTimeout(() => {
									_self.jumpUrl('login/login', 'new');
								}, 500);
							}else{
								api.showToast('操作失败，请稍后再试', 2000);
							}
				        }
				    }
				});
			},
			jumpUrl(uri = 'login/login', type = 'new') {
				if(_self.isLogin || uri=='login/login' || uri=='login/register' || uri=='setting/about'){
					api.jumpUrl('/pages/' + uri, type);
				}else{
					api.showToast('请先登录账号');
					setTimeout(() => {
						_self.jumpUrl('login/login', 'new');
					}, 500);
				}
			},
		},
		onPullDownRefresh() {
			if(_self.isLogin) _self.getUserInfo();
			uni.stopPullDownRefresh();
		}
	}
</script>

<style>
	@import url('@/components/loading/loading.css');
	.y-sign{
		color: #FFEEDF;
		text-align: right;
		height: 40upx;
		line-height: 40upx;
	}
	.n-sign{
		background-image: linear-gradient(to right, rgba(255,12,208,0.8), #dd0aeb);
		height: 40upx;
		line-height: 40upx;
		border-radius: 10upx;
		text-align: center;
	}
	.sign text{
		display: block;
	}
	.sign{
		font-size: 24upx;
		display: inline-block;
		margin-top: -35upx;
		//background-color: #007AFF;
	}
	.nologin .head-content-login{
		display: inline-block;
		width: 200upx;
		height: 60upx;
		line-height: 60upx;
		background-color: none;
		color: rgba(255,12,208,0.8)!important;
		border-radius: 60upx;
		font-size: 30upx!important;
		text-align: center;
		border: 1px solid rgba(255,12,208,0.8);
	}
	.nologin{
		margin: 30upx 30upx 130upx;
	}
	.vip-date{
		color: #FFEEDF;
		margin-left: 10upx;
	}
	.user-vip{
		background-image: linear-gradient(to right,rgba(255,12,208,0.8), rgba(254,67,101,0.8))!important;
		color: #FFFFFF;
	}
	.user-leve, .user-vip{
		padding: 3upx 20upx;
		background-color: #CCCCCC;
		border-radius: 8upx;
	}
	.content-out{
		width: 90%;
		height: 80upx;
		line-height: 80upx;
		text-align: center;
		background-image: linear-gradient(to right,rgba(255,12,208,0.8), rgba(254,67,101,0.8));
		margin: 30upx auto;
		font-size: 30upx;
		color: #FFFFFF;
		border-radius: 20upx;
	}
	.bottom-content-line text{
		margin-left: 20upx;
		font-size: 30upx;
		color: #666666;
	}
	.bottom-content-line .left-img{
		width: 50upx;
		height: 50upx;
		margin-top: 25upx;
	}
	.bottom-content-line .right-img{
		width: 30upx;
		height: 30upx;
		margin-top: 35upx;
	}
	.bottom-content-line{
		height: 100upx;
		line-height: 94upx;
		padding: 0 30upx;
		//background-color: #007AFF;
	}
	.bottom-content{
		padding: 20upx 0;
	}
	.center-content-item image{
		width: 70upx;
		height: 70upx;
	}
	.center-content-item{
		width: 25%;
		font-size: 25upx;
		text-align: center;
		display: inline-block;
		margin-bottom: 20upx;
		color: #666666;
	}
	.center-content{
		//height: 300upx;
		border-radius: 40upx 40upx 0 0;
		box-shadow: 0px -6px 6px 0px rgba(103, 103, 103, 0.2);
		padding: 40upx 0 20upx;
	}
	.head-content-bottom{
		//height: 150upx;
		margin: 20upx 0;
		border-radius: 20upx;
		background-image: linear-gradient(to right, #24292E, rgba(0,0,0,0.3));
		color: #FFFFFF;
		padding: 20upx 20upx 15upx;
		font-size: 30upx;
	}
	.head-content-center-left{
		margin-right: 6%;
		background-image: linear-gradient(to right,rgba(255,12,208,0.8), rgba(254,67,101,0.8));
	}
	.head-content-center-right{
		background-image: linear-gradient(to right,rgba(255,12,208,0.8), #dd0aeb);
	}
	.head-content-center view{
		width: 47%;
		height: 90upx;
		display: inline-block;
		border-radius: 20upx;
	}
	.head-content-center image{
		width: 80upx;
		height: 80upx;
		margin: 5upx 0 0 10upx;
	}
	.head-content-center text{
		font-size: 30upx;
		color: #FFFFFF;
		display: block;
	}
	.head-content-center{
		margin-top: 50upx;
	}
	.head-content-top text{
		margin-left: 20upx;
		font-weight: bold;
		color: #000000;
		font-size: 40upx;
	}
	.head-content-top .right-img{
		width: 40upx;
		height: 40upx;
		margin-top: 30upx;
	}
	.head-content-top .tx{
		width: 100upx;
		height: 100upx;
		border-radius: 50%;
		box-shadow: 2px 4px 6px 0px rgba(103, 103, 103, 0.3);
		//border: 1px solid #E5E5E5;
	}
	.head-content-top{
		height: 100upx;
		line-height: 100upx;
		//background-color: #007AFF;
	}
	.head-content{
		padding: 50upx 40upx 0;
		//background-color: #ffffff;
	}
	.right{
		float: right;
	}
	.left{
		float: left;
	}
	.line{
		height: 20upx;
		background-color: #F8F8F8;
	}
</style>
